    beforeDestroy() {
      clearTimeout(this.timer);
    },
    methods : {
        async initData() {
            this.panelData();
            this.queryRepurchList();
            this.queryMdexCharts();
            this.queryBoardroom();
            this.queryGasPrice();
            this.queryBalances();
        },
        queryGasPrice() {
            const mdex_api  =   "https://gateway.mdex.cc/panel/gas";
            try {
                this.axios.get(`${mdex_api}`).then ( async res =>{
                    const result = res.data;
                    if (result.code == 0) {
                        this.mdex_panel.heco_gas =   result.result;
                    }
                });
            }catch (error) {
                console.log(error);
            }
        },
        async lastRecords () {
            dayjs.extend(relativeTime);
            dayjs.locale('zh-cn');

            const repurch       =   await this.getTransInfo('0x46900C0c18ace98bAAB81561B9906Dc93287910C');
            const oracle        =   await this.getTransInfo('0x7b4B0d9Cd226017eA3875D49196Ea63A6ea8C278');
            const rep_oracle    =   await this.getTransInfo('0x656584dD2FE7a24c85c4F3Abb707B3848C193889');

            const last_opts =   [
                { "opt_name" : "回购销毁", "opt_record" : dayjs(Number(repurch.timeStamp + '000')).fromNow(), 'opt_desc' : "回购价格低于现价，立即执行9秒/次回购。"},
                { "opt_name" : "回购价格预言机", "opt_record" : dayjs(Number(rep_oracle.timeStamp + '000')).fromNow(), 'opt_desc' : "每隔72小时定时执行一次"},
                { "opt_name" : "交易对价格预言机", "opt_record" : dayjs(Number(oracle.timeStamp + '000')).fromNow(), 'opt_desc' : "每隔30分定时执行一次"},
                { "opt_name" : "-", "opt_record" : '', 'opt_desc' : ""},
                { "opt_name" : "-", "opt_record" : '', 'opt_desc' : ""}
            ];
            this.last_opts  =   last_opts;
        },
        async getTransInfo (address) {
            const heco_api  =   "https://api.hecoinfo.com/api";
            let tran_data   =   [];

            try {
                const res = await this.axios.get(`${heco_api}`, {
                    params: {
                        'module': 'account',
                        'action': 'txlist',
                        'address': address,
                        'startblock': 0,
                        'endblock': 99999999,
                        'page': 1,
                        'offset': 1,
                        'sort': 'desc',
                        'apikey': 'EQQZEVRPETY4IHGJ1DDSRH6Q29QHHAH9A2'
                    }
                })

                if (res.status == 200 && res.data.status == 1) {
                    const resp_data =   res.data.result;
                    tran_data       =   resp_data[0];
                }
            }catch (error) {
                console.log(error);
            }

            return tran_data;
        },
        async panelData () {
            // 检查Heco节点区块同步信息
            const mdex_api  =   "https://gateway.mdex.cc/panel/blocks";
            try {
                this.axios.get(`${mdex_api}`).then ( async res =>{
                    const result    =   res.data;
                    if (result.code == 0) {
                        const block_data    =   result.result;
                        const heco_blocks   =   [
                            {rpc_name: 'Heco 官方节点', block_number: block_data.block_number},
                            {rpc_name: 'Master RPC节点', block_number: block_data.master_block},
                            {rpc_name: 'Slave RPC节点', block_number: block_data.slave_block},
                            {rpc_name: 'Fast RPC节点', block_number: block_data.fast_block},
                            {rpc_name: 'Private RPC节点', block_number: block_data.priv_block}
                        ];
                        this.heco_blocks    =   heco_blocks;
                    }
                });
            }catch (error) {
                console.log(error);
            }

            // 回购合约USDT余额
            const erc20_sol     =   erc20Contract(HecoWeb3, "0xa71edc38d189767582c38a3145b5873052c3e47a");
            const repurch_usdt  =   await erc20_sol.contract.methods.balanceOf('0x46900c0c18ace98baab81561b9906dc93287910c').call();
            this.boardroom.repurch_usdt =   Number(repurch_usdt / 1e18);
        },
        async queryBalances() {
            const repurch_addrs     =   this.accounts.repurch;
            const repurch_data      =   [];
            for (const repurch of repurch_addrs) {
                const balance   =   await HecoWeb3.eth.getBalance(repurch);

                repurch_data.push({
                    address: repurch,
                    balance: Number(balance / 1e18),
                    addr_type: '回购地址'
                })
            }
            this.repurch_data   =   repurch_data;

            const oracle_addrs     =   this.accounts.oracle;
            const oracle_data      =   [];
            for (const oracle of oracle_addrs) {
                const addr_type =   oracle == '0xa6420fe4f01A53669C7285A536464564fc157034'  ?   '回购预言机地址'   :   '交易对预言机地址';
                const balance   =   await HecoWeb3.eth.getBalance(oracle);

                oracle_data.push({
                    address: oracle,
                    balance: Number(balance / 1e18),
                    addr_type
                })
            }
            this.oracle_data   =   oracle_data;

            this.getTransInfo('0x46900C0c18ace98bAAB81561B9906Dc93287910C');
        },
        async queryRepurchList() {
            const hecoinfo_api  = "https://api.hecoinfo.com/api";
            try {
                this.axios.get(`${hecoinfo_api}`, {
                params: {
                    module: 'account',
                    action: 'txlist',
                    address: '0x46900C0c18ace98bAAB81561B9906Dc93287910C',
                    startblock: 0,
                    endblock: 99999999,
                    page: 1,
                    offset: 10,
                    sort: 'desc'
                }}).then ( async res =>{
                    const result = res.data;
                    if (result.status == 1) {
                        const repurch_data  =   [];
                        const repurch_list  = result['result'];
                        for (const repurch of repurch_list) {
                            const times = dayjs(Number(repurch.timeStamp + '000')).format('YYYY-MM-DD HH:mm:ss');

                            repurch_data.push({
                                block_number: repurch.blockNumber,
                                hash: repurch.hash,
                                from: repurch.from,
                                to: repurch.to,
                                repurch_time: times
                            })
                        }

                        this.repurch_list   =   repurch_data;
                    }
                });
            }catch (error) {
                console.log(error);
            }
        },
        queryMdexCharts() {
            const mdex_api  =   "https://gateway.mdex.cc/v2/mdex/charts?chainid=128";
            try {
                this.axios.get(`${mdex_api}`).then ( async res =>{
                    const result    =   res.data;
                    if (result.code == 0) {
                        this.mdex_panel.charts      =   result.result;
                        this.mdex_panel.mdex_info   =   result.result.mdex_info;
                    }
                });
            }catch (error) {
                console.log(error);
            }
        },
        queryBoardroom() {
            const mdex_api  =   "https://api.mdex.com/mdex/rewards";
            try {
                this.axios.get(`${mdex_api}`).then ( async res =>{
                    const result    =   res.data;
                    if (result.code == 0) {
                        this.boardroom.rewards    =   result.result;
                    }
                });
            }catch (error) {
                console.log(error);
            }
        },
        useNumberFilter (value, cut) {
            return numberFilter(value, cut);
        },
        showRepurch(index) {
            window.open('https://hecoinfo.com/address/' + this.repurch_data[index]['address'], '_blank').location;
        },
        showOracle(index) {
            window.open('https://hecoinfo.com/address/' + this.oracle_data[index]['address'], '_blank').location;
        },
        repurchInfo(index, stype='hash') {
            if (stype == 'from') {
                window.open('https://hecoinfo.com/address/' + this.repurch_list[index]['from'], '_blank').location;
            } else if (stype == 'to') {
                window.open('https://hecoinfo.com/address/' + this.repurch_list[index]['to'], '_blank').location;
            } else {
                window.open('https://hecoinfo.com/tx/' + this.repurch_list[index]['hash'], '_blank').location;
            }
        },
        contractInfo(index) {
            window.open('https://hecoinfo.com/address/' + this.constracts[index]['address'], '_blank').location;
        }
    }
}
</script>
