function cutString(str) { 
    let last = 0;
    let all = str.length;
    let fisrt = str.substring(0,6);

    if (str.lastIndexOf('（') == -1) {
        if (str.lastIndexOf('(') == -1) {
            last = all - 5;
        }else{
            last = str.lastIndexOf('(');
        }
    }else{
        last = str.lastIndexOf('（');
    }
    
    if (all > 13) {
        str =   fisrt+" ... "+str.substring(last,all);
    }

    return str;
}

function timestampToTime(timestamp) {
    var date = new Date(timestamp * 1000);
    var Y = date.getFullYear() + '-';
    var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
    var D = date.getDate() + ' ';
    var h = date.getHours() + ':';
    var m = date.getMinutes() + ':';
    var s = date.getSeconds();
    return Y + M + D + h + m + s;
}

function numberFilter (value, cut = 2) {
    //value为我们传进来的数据 比如  145775.422346
    //cut 为需要保留的小数位数  -1为清空小数 0为保留全部位数的小数 传入多少即为多少 不传默认保留两位小数 传进来多少就截取多少
    //数据校验  
    if (parseFloat(value).toString() == 'NaN') return '0.00'
    // 将数值截取
    let num = value.toString().split('.')
    let zs = num[0]
    let xs = num[1]
    // 整数部分处理，增加,
    const intPartFormat = zs.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
    if (xs != null) {
      if(cut == 0){
        return intPartFormat + '.' + xs
      }else if(cut == -1){
        return intPartFormat
      }else{
        return intPartFormat +'.' + xs.substr(0,cut)
      }
    } else {
      return intPartFormat
    }
}

module.exports = {
	cutString,
    timestampToTime,
    numberFilter
}